import React, {useState} from "react";
import {MDBBtn, MDBCol, MDBContainer, MDBRow,} from "mdbreact";
import Step from "./Step";
import BackBtn from "../../assets/back-btn.png";
import {fieldValidator} from "./validator";
import CustomCheckBox from "../../shared/utility";
import {FormControlLabel} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";

const Step81 = props => {
    const {state, prevStep, nextStep, history} = props;
    const [errorMsg, setErrorMsg] = useState({});

    const onChange = (e, fieldName, value) => {
        let updatedState = {...state};
        let errors = {...errorMsg};

        switch (fieldName) {
            case 'everBeenInProceedings':
            case 'everFiledForm407':
                updatedState[fieldName] = value;
                setErrorMsg(fieldValidator(fieldName, updatedState, 'required', errors).error);
                break;
            default:
                break;
        }
        props.onChangeState(updatedState);
    };


    const onNext = () => {
        let errors = {};

        errors = fieldValidator('everBeenInProceedings', state, 'required', errors).error;
        let newForm = fieldValidator('everFiledForm407', state, 'required', errors);

        if (newForm.isValid) {
            props.history.push(nextStep);
        } else {
            setErrorMsg(newForm.error);
        }
    };

    return (
        <Step heading={`Processing Information (Continued ..)`}>
            <img
                src={BackBtn}
                className="btn-back"
                alt={`Back`}
                onClick={() => {
                    if(state.typeOfLocation === "withImmigrantVisa"){
                        history.push(prevStep);
                    } else {
                        history.push('/start/7');
                    }
                }}
            />
            <MDBContainer>
                <MDBRow center>
                    <MDBCol sm={`10`} md={`8`}>

                        <MDBRow center className={`mb-2`}>

                            <h4 className={`mb-2 text-center`}>
                                Have you ever been in exclusion, deportation, or removal proceedings or ordered removed
                                from the United States?
                            </h4>

                            <FormControlLabel
                                className={`mb-0 mt-2`}
                                control={
                                    <CustomCheckBox
                                        id={`proceedingYes`}
                                        checked={state.everBeenInProceedings === "Yes"}
                                        onChange={e => onChange(e, "everBeenInProceedings", "Yes")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(
                                    <span className={`labelClass`}>
                                        Yes
                                    </span>
                                )}
                            />
                            <FormControlLabel
                                className={`mb-0 mt-2`}
                                control={
                                    <CustomCheckBox
                                        id={`proceedingNo`}
                                        checked={state.everBeenInProceedings === "No"}
                                        onChange={e => onChange(e, "everBeenInProceedings", "No")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(
                                    <span className={`labelClass`}>
                                        No
                                    </span>
                                )}
                            />
                        </MDBRow>

                        <MDBRow center className={`mb-2`}>

                            <h4 className={`mb-2 text-center`}>
                                Since you were granted permanent residence, have you ever filed form I-407, Abandonment
                                by
                                Alien of Status as Lawful Permanent Resident, or otherwise been determined to have
                                abandoned
                                your status?
                            </h4>

                            <FormControlLabel
                                className={`mb-0 mt-2`}
                                control={
                                    <CustomCheckBox
                                        id={`abandonedYes`}
                                        checked={state.everFiledForm407 === "Yes"}
                                        onChange={e => onChange(e, "everFiledForm407", "Yes")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(
                                    <span className={`labelClass`}>
                                        Yes
                                    </span>
                                )}
                            />
                            <FormControlLabel
                                className={`mb-0 mt-2`}
                                control={
                                    <CustomCheckBox
                                        id={`abandonedNo`}
                                        checked={state.everFiledForm407 === "No"}
                                        onChange={e => onChange(e, "everFiledForm407", "No")}
                                        color="primary"
                                        inputProps={{'aria-label': 'secondary checkbox'}}
                                    />
                                }
                                label={(
                                    <span className={`labelClass`}>
                                        No
                                    </span>
                                )}
                            />
                        </MDBRow>

                        <MDBRow center className="d-flex flex-column align-items-center">
                            {Object.keys(errorMsg).length > 0 && (
                                <div className="text-danger mb-2">Please Fill out all the required fields</div>
                            )}
                            <MDBBtn
                                color="pink"
                                rounded
                                type="button"
                                className="z-depth-1a"
                                onClick={() => onNext()}
                            >
                                Next
                            </MDBBtn>
                        </MDBRow>
                    </MDBCol>
                </MDBRow>
            </MDBContainer>
        </Step>
    );
}

export default Step81;
