import React, {Component} from "react";
import {MuiPickersUtilsProvider} from '@material-ui/pickers';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import {Redirect, Route, Switch} from "react-router-dom";
import axios from 'axios';
import Step1 from "./Step1";
import Step2 from "./Step2";
import Step3 from "./Step3";
import Step4 from "./Step4";
import Step5 from "./Step5";
import Step6 from "./Step6";
import Step7 from "./Step7";
import Step8 from "./Step8";
import Step9 from "./Step9";
import Step10 from "./Step10";
import Step11 from "./Step11";
import Step12 from "./Step12";
import Step13 from "./Step13";
import Step14 from "./Step14";
import Step15 from "./Step15";
import Step16 from "./Step16";
import Step17 from "./Step17";
import Step18 from "./Step18";
// import AllOptions from "./AllOptions";
import ThankYou from "./ThankYou";
import Decline from "./Decline";
import Step81 from "./Step81";
import Step19 from "./Step19";

export default class MultiStepForm extends Component {
    state = {
        bioGraphicInfo: {},
        cardDetails: {},
        mailAddress: {}
    };

    /*handleChangeField = (e = {}, name, value) => {
        this.setState(
            {
                [name]: value,
            },
            () => console.log(this.state)
        );
    };*/

    handleChangeState = newState => {
        this.setState(newState);
    }

	linkRef = React.createRef();
    submitData() {

        const state = this.state;

        const reqBody = {
            Part1Line1AlienNumber: state.arNumber,
            Part1Line2AcctIdentifier: state.uscisOAN,
            Part1Line3aFamilyName: state.lname,
            Part1Line3bGivenName: state.fname,
            Part1Line3cMiddleName: state.mName,
            Part1Line4checkbox4: state.isPrevNames ? 'Y' : 'N',
            Part1Line6aInCareofName: state.mailAddress.name,
            Part1Line6bStreetNumberName: state.mailAddress && state.mailAddress.street,
            Part1Line6dCityOrTown: state.mailAddress.city,
            Part1Line6eState: state.mailAddress.state,
            Part1Line6fZipCode: state.mailAddress.zipCode,
            Part1Line6iCountry: state.mailAddress.country,
            Part1Line5aFamilyName: state.prevlname,
            Part1Line5bGivenName: state.prevfname,
            Part1Line5cMiddleName: state.prevMName,
            Part1Line7aStreetNumberName: state.physAddress && state.physAddress.street,
            Part1Line7cCityOrTown: state.physAddress && state.physAddress.city,
            Part1Line7eZipCode: state.physAddress && state.physAddress.zipCode,
            Part1Line7dState: state.physAddress && state.physAddress.state,
            Part1Line7hCountry: state.physAddress && state.physAddress.country,
            Part1Line8Gender: state.gender,
            Part1Line9DateOfBirth: state.dateOfBirth,
            Part1Line10CityTownOfBirth: state.bornAddress && state.bornAddress.city,
            Part1Line11CountryofBirth: state.bornAddress && state.bornAddress.country,
            Part1Line12MotherGivenName: state.motherName,
            Part1Line13FatherGivenName: state.fatherName,
            Part1Line14ClassOfAdmission: state.classAdmission,
            Part1Line15DateOfAdmission: state.dateOfAdmission,
            Part1Line1chk1: state.status,
            Part2Line1chk2: state.type,
            Part2Line2chk: state.type,
            Part3Line1LocationAppliedVisa: state.processingAddress && state.processingAddress.street,
            Part3Line1LocationIssuedVisa: state.processingAddress && state.processingAddress.street,
            Part3Line3Destination: state.destInUSA,
            Part3Line3CityState: state.poeAdmitedAddr && state.poeAdmitedAddr.street,
            Part3Checkbox4: state.everBeenInProceedings,
            Part3Checkbox5: state.everFiledForm407,
            hispanicOrLatino: state.bioGraphicInfo && state.bioGraphicInfo.hispanicOrLatino ? 'H' : 'N',
            Part3chkRace: state.bioGraphicInfo && state.bioGraphicInfo.race,
            feet: state.bioGraphicInfo && state.bioGraphicInfo.feet,
            inch: state.bioGraphicInfo && state.bioGraphicInfo.inch,
            Part3weight1: state.bioGraphicInfo && state.bioGraphicInfo.weight && state.bioGraphicInfo.weight.split('')[0],
            Part3weight2: state.bioGraphicInfo && state.bioGraphicInfo.weight && state.bioGraphicInfo.weight.split('')[1],
            Part3weight3: state.bioGraphicInfo && state.bioGraphicInfo.weight && state.bioGraphicInfo.weight.split('')[2],
            eyeColor: state.bioGraphicInfo && state.bioGraphicInfo.eyeColor,
            hairColor: state.bioGraphicInfo && state.bioGraphicInfo.hairColor,
            isDisabled: state.bioGraphicInfo && state.bioGraphicInfo.isDisabled,
            Part4Line1chk1a: state.bioGraphicInfo && state.bioGraphicInfo.disabilityInfo && state.bioGraphicInfo.disabilityInfo.hasOwnProperty('deaf') ? 'Y' : 'N',
            Part4Line1AccomodationReq: state.bioGraphicInfo && state.bioGraphicInfo.disabilityInfo && state.bioGraphicInfo.disabilityInfo.deaf && state.bioGraphicInfo.disabilityInfo.deaf.descDeaf,
            Part4Line1BlindAccomodtion: state.bioGraphicInfo && state.bioGraphicInfo.disabilityInfo && state.bioGraphicInfo.disabilityInfo.hasOwnProperty('blind'),
            Part4Line1BlindAccomodationReq: state.bioGraphicInfo && state.bioGraphicInfo.disabilityInfo &&  state.bioGraphicInfo.disabilityInfo.blind && state.bioGraphicInfo.disabilityInfo.blind.descBlind,
            Part4Line1cOthers: state.bioGraphicInfo && state.bioGraphicInfo.disabilityInfo &&  state.bioGraphicInfo.disabilityInfo.hasOwnProperty('other'),
            Part4Line1cAccomodationReq: state.bioGraphicInfo && state.bioGraphicInfo.disabilityInfo &&  state.bioGraphicInfo.disabilityInfo.other && state.bioGraphicInfo.disabilityInfo.other.descOther,
            Part5chkboxLanguage: state.understandEnglish,
            Part5Line1bLanguage: state.language,
            Part5authorizechk: state.preparerNeeded,
            Part5Line2Preparername: state.preparerName,
            Part5ApplicantDayTimePhn: state.teleNumber,
            Part5Line4Mobile: state.mobileNumber,
            Part5PreparerEmailAddress: state.email,
            interpreterLastName: state.interpreterLastName,
            interpreterFirstName: state.interpreterFirstName,
            interpreterBussiness: state.interpreterOrg,
            Part6Line3SteetNumber: state.interpreterMailAddr && state.interpreterMailAddr.street,
            PartLine3cCityTown: state.interpreterMailAddr && state.interpreterMailAddr.city,
            Part6Line3dstate: state.interpreterMailAddr && state.interpreterMailAddr.state,
            Part6Line3eZipcode: state.interpreterMailAddr && state.interpreterMailAddr.zipCode,
            Part6Line3hCountry: state.interpreterMailAddr && state.interpreterMailAddr.country,
            Part6Line4InterpretersphoneNumber: state.interpreterTeleNumber,
            Part6Line4InterpretersmobileNumber: state.interpreterMobileNumber,
            Part6Line5InterpretersEmailAddress: state.interpreterEmail,
            Part7Line1FamilyName: state.preparerLastName,
            Part7Line1bGivenName: state.preparerFirstName,
            Part7Line2NameBusinessor: state.preparerOrg,
            Part7Line3StreetNumbername: state.preparerMailAddr && state.preparerMailAddr.street,
            Part7Line3cCityTown: state.preparerMailAddr && state.preparerMailAddr.city,
            Part7Line3dState: state.preparerMailAddr && state.preparerMailAddr.state,
            Part7Line3eZipcode: state.preparerMailAddr && state.preparerMailAddr.zipCode,
            Part7Line4Phnnumber: state.preparerTeleNumber,
            Part7Line5FaxNumber: state.preparerMobileNumber,
            Part7Line6EmailAddress: state.preparerEmail
        };

		const proxyurl = "https://cors-anywhere.herokuapp.com/";
        const url = 'https://migrationade.com/api/i90';

		if((Object.keys(state.bioGraphicInfo).length > 0)  || (Object.keys(state.cardDetails).length > 0) || (Object.keys(state.mailAddress).length > 0)){
		 axios.post((proxyurl+url), reqBody,{
                headers: {
					'Content-Type' : 'application/json',
					'Authorization' : 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.IjVmNzY4ZDQyOWY3YTc3ZjhlZTA4ZDRiMTFlY2NkYmVhMzRmM2Q3ZDAwYTVkNGRhMGYwZjgwNWEyZjJhNjVhYzAyZThlMTk3ZDIyM2I2NWY3NjhkNDI5ZjdjYmI2YTgzMWU3NzQyNTdiZmEyNDAzYjQxMThiOThkZDFhNWY3NjhkNDI5ZjdmZiI.E1dSmiJ60WVtbGDiuc66_YpW75GEZHDNEoHVi_FS6PA',
					'Access-Control-Allow-Credentials': 'true'
				  }
              })
            .then((response) => {
				if(response.status === 200){
					const href = response.data.data.link;
					const a = this.linkRef.current;
					a.download = 'MyForm.pdf';
					a.href = href;
					a.target= "_blank";
					a.click();
					a.href = '';
				}
			})
            .catch((error)=> console.log('Error::',error))
            .finally(() => console.log("Data Sent", {data: state}));
		}
    }

    
    renderStep = (props) => {
        const {history} = props;
        const {path, params} = props.match;
        const currentPath = `/${path.split("/")[1]}`;
        if (params.step === "thankyou") {
            this.submitData();
            return <ThankYou history={history} nextStep={`${currentPath}/1`}/>;
        } else if (params.step === "decline") {
            return <Decline/>;
        }
        const step = parseInt(params.step);
        switch (step) {
            case 1:
                return (
                    <Step1
                        history={history}
                        nextStep={`${currentPath}/2`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 2:
                return (
                    <Step2
                        history={history}
                        prevStep={`${currentPath}/1`}
                        nextStep={`${currentPath}/3`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 3:
                return (
                    <Step3
                        history={history}
                        prevStep={`${currentPath}/2`}
                        nextStep={`${currentPath}/4`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 4:
                return (
                    <Step4
                        history={history}
                        prevStep={`${currentPath}/3`}
                        nextStep={`${currentPath}/5`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 5:
                return (
                    <Step5
                        history={history}
                        prevStep={`${currentPath}/4`}
                        nextStep={`${currentPath}/6`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 6:
                return (
                    <Step6
                        history={history}
                        prevStep={`${currentPath}/5`}
                        nextStep={`${currentPath}/7`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 7:
                return (
                    <Step7
                        history={history}
                        prevStep={`${currentPath}/6`}
                        nextStep={`${currentPath}/8`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 8:
                return (
                    <Step8
                        history={history}
                        prevStep={`${currentPath}/7`}
                        nextStep={`${currentPath}/81`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 9:
                return (
                    <Step9
                        history={history}
                        prevStep={`${currentPath}/81`}
                        nextStep={`${currentPath}/10`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 10:
                return (
                    <Step10
                        history={history}
                        prevStep={`${currentPath}/9`}
                        nextStep={`${currentPath}/11`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 11:
                return (
                    <Step11
                        history={history}
                        prevStep={`${currentPath}/10`}
                        nextStep={`${currentPath}/12`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 12:
                return (
                    <Step12
                        history={history}
                        prevStep={`${currentPath}/11`}
                        nextStep={`${currentPath}/13`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 13:
                return (
                    <Step13
                        history={history}
                        prevStep={`${currentPath}/12`}
                        nextStep={`${currentPath}/14`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 14:
                return (
                    <Step14
                        history={history}
                        prevStep={`${currentPath}/13`}
                        nextStep={`${currentPath}/15`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 15:
                return (
                    <Step15
                        history={history}
                        prevStep={`${currentPath}/14`}
                        nextStep={`${currentPath}/16`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 16:
                return (
                    <Step16
                        history={history}
                        prevStep={`${currentPath}/15`}
                        nextStep={`${currentPath}/17`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 17:
                return (
                    <Step17
                        history={history}
                        prevStep={`${currentPath}/16`}
                        nextStep={`${currentPath}/18`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 18:
                return (
                    <Step18
                        history={history}
                        prevStep={`${currentPath}/17`}
                        nextStep={`${currentPath}/19`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 19:
                return (
                    <Step19
                        history={history}
                        prevStep={`${currentPath}/18`}
                        nextStep={`${currentPath}/thankyou`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            case 81:
                return (
                    <Step81
                        history={history}
                        prevStep={`${currentPath}/8`}
                        nextStep={`${currentPath}/9`}
                        state={this.state}
                        onChangeState={this.handleChangeState}
                    />
                );
            default:
                return <div>Invalid Step</div>;
        }
    };

    render() {
        const {path} = this.props.match;
        return (
            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <Switch>
                    <Route
                        path="/start"
                        exact
                        render={() => <Redirect to={`${path}/1`}/>}
                    />
                    <Route path={`${path}/:step`} render={(props) => <>
                        {this.renderStep(props)}
                    </>}/>
                </Switch>
				 <a ref={this.linkRef}/>
            </MuiPickersUtilsProvider>
        );
    }
}
